
.login {
    display: -ms-flexbox;
    display: flex;
    width: 28rem;
    border: 0;
    -ms-flex-item-align: center;
        align-self: center;
    background-color: #FFFFFF;
}
    
.login__body {
    position: relative;
    text-align: center;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    padding: 1.25rem; 
}

.login__body input {
    margin: 10px 10px;
} 
    
.login .header__logo {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    font-family: sans-serif;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    font-size: 1.8rem;
    text-align: center; 
}

.login .header__logo span {
    display: block; 
}

.login .header__logo span:last-child {
    font-size: 0.95rem;
}
          

@media (max-width: 500px) {
    .login, .login__body {
        width: 100%;
        padding: 0.5em;
    }
}

@media (max-height: 580px) {
    .login {
        margin-top: 30px;
    }
  }


