.side-navbar {
    min-width: 230px;
    max-width: 230px;
    min-height: 100vh;
    background-color: #1C2331;
    color: #FFF;
    z-index: 100;
}

.side-navbar > div {
    min-width: 230px;
    max-width: 230px;
    top: 0;
    bottom: 0;
    position: fixed;
    overflow-y: auto;
    overflow-x: hidden;
}

.side-navbar li {
    display: -ms-flexbox;
    display: flex;
    list-style-type: none;
    margin: 10px 10px;
    height: 40px;
    -ms-flex-line-pack: center;
        align-content: center;
}

.side-navbar li:first-of-type {
    height: 100px;
}

.side-navbar a {
    text-decoration: none;
    background-color: #1C2331;
    color: #FFF;
    width: 100%;
    line-height: 40px;
    -webkit-box-shadow: none;
            box-shadow: none;
    font-size: 15px;
    text-align: start;
    padding: 0 10px;
}

.side-navbar a:hover {
    background-color: #3F729B;
}

.usedLink {
    background-color: #3F729B;
}

.side-navbar .logo {
    width: 30%;
    height: auto;
    margin-right: 5px;
}


.side-navbar .container-logo {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    font-size: 1.4em;
    margin: 5px;
}


.side-navbar .navbar-nav {
    -ms-flex-direction: column;
        flex-direction: column;
} 

.side-navbar .collapsing {
    -webkit-transition-property: width;
    -o-transition-property: width;
    transition-property: width;
} 

.side-navbar .collapse {
    width: 0;
} 

.side-navbar .collapsed {
    widows: 200px;
}

@media (max-width: 1300px) {
    .side-navbar {
        position: fixed;
        z-index: 2000;
        margin-left: -100%;
        -webkit-transition: margin-left .3s linear;
        -o-transition: margin-left .3s linear;
        transition: margin-left .3s linear;
    }

    .hidden {
        margin-left: 0;
    }
}


