.pop-up-success {
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    top: 75px;
    right: 5px;
    z-index: 2000;
    width: 280px;
    background-color: #4CAF50;
    color: #FFF;
    padding: 15px 20px;
    text-align: center;
    -webkit-box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.16), 0 0 2px 1px rgba(0, 0, 0, 0.12);
            box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.16), 0 0 2px 1px rgba(0, 0, 0, 0.12);
}

.pop-up-fail {
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    top: 75px;
    right: 5px;
    z-index: 2000;
    width: 280px;
    background-color: rgb(175, 76, 76);
    color: #FFF;
    padding: 15px 20px;
    text-align: center;
    -webkit-box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.16), 0 0 2px 1px rgba(0, 0, 0, 0.12);
            box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.16), 0 0 2px 1px rgba(0, 0, 0, 0.12);
}

.message-enter {
    opacity: 0.01;
}

.message-enter-active {
    opacity: 1;
    -webkit-transition: all 300ms ease-out;
    -o-transition: all 300ms ease-out;
    transition: all 300ms ease-out;
}

.message-exit {
    opacity: 1;
}

.message-exit-active {
    opacity: 0.01;
    -webkit-transition: all 300ms ease-out;
    -o-transition: all 300ms ease-out;
    transition: all 300ms ease-out;
}


