.navbar {
    background-color: #3F729B;
    -webkit-box-shadow: none;
            box-shadow: none;
}

.nav-link, .nav-item {
    color: #f8f9fa;
}

.navbar-nav {
    -ms-flex-direction: row;
        flex-direction: row;
}

.navbar-expand-lg .navbar-nav {
    right: 0;
    left: auto;
}

.menu-icon {
    display: none;
    font-size: 2em;
    color: #FFF;
}

@media (max-width: 1300px) {
    .menu-icon {
        display: block;
    }
}


