.search-form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: baseline;
        align-items: baseline;
    width: 90%;
    margin: auto;
    border-radius: .25rem;
    padding: 20px;
    margin-bottom: 20px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.search-form > * {
    margin: 0 10px;
}

.content-header {
    font-size: 2.5em;
    font-weight: 600;
    margin: 1em auto;
}


@media (max-width: 500px) {
    .custom-btn {
        width: 80%;
    }
  }



