.contact {
    width: 100%;
    max-width: 1250px;
    margin: 1.5em auto;
    border-radius: 5px;
    padding: 10px;
}

.contact__header {
    display: -ms-flexbox;
    display: flex;
    margin-left: 10px;
    margin-right: 10px;
    -ms-flex-wrap: wrap-reverse;
        flex-wrap: wrap-reverse;
}

@media (max-width: 760px) {
    .contact__header {
        -ms-flex-pack: center;
            justify-content: center;
    }

    .contact__item {
        width: 128px;
        border-radius: 5px;
        margin: 2px;
    }
}

@media (max-width: 500px) {
    .contact {
      padding: 0;
    }
  }

.contact__item {
    display: -ms-flexbox;
    display: flex;
    background-color: #f5f5f5;
    width: 140px;
    height: 40px;
}

.contact__item--active {
    position: relative;
    z-index: 1000;
    background-color: #00838f;
}

.contact__item--active .contact__link {
    color: #FFFFFF;
}

.contact__link {
    display: -ms-flexbox;
    display: flex;
    color: #5e5e5e;
    text-decoration: none;
    font-weight: 500;
    width: 100%;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
}

.contact__content {
    position: relative;
    z-index: 500;
}

.content__form {
    background: #FFFFFF;
}

.content__tab {
    display: none;
    min-height: 495px;
}

.content__tab--active {
    display: -ms-flexbox;
    display: flex;
}

.contact .custom-table th::after {
    content: none;
}

.contact .custom-table th::before {
    content: none;
}

.contact .custom-table {
    min-height: 200px;
}


