.form {
    width: 100%;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    padding: 3em;
}

@media (max-width: 760px) {
    .form {
      padding: 20px 10px;
    }
  }

  @media (max-width: 460px) {
    .form {
      padding: 10px 0;
    }
  }

.form__field {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    width: 32.43%;
    margin: 5px;
}

.form__field--long {
    width: 100%;
}

.form__input {
    font-size: 16px;
    outline: 0;
    padding: 5px;
}


.form__input[disabled] {
    background-color: #b1b1b1;
}

.form__submit {
    margin: 10px auto auto auto;
    background-color: #4285f4;
    color: #fff;
    border: 0;
    outline: 0;
    font-size: 17px;
    padding: 10px 30px;
    border-radius: 0.125em;
    -webkit-box-shadow: 0 1px 3px 1px rgba(0, 0, 0, 0.3);
            box-shadow: 0 1px 3px 1px rgba(0, 0, 0, 0.3);
}

.form__submit:active {
    background-color: #0b51c5;
}


